// src/components/ui/LanguageSelector.tsx
import { useTranslation } from 'react-i18next';
import { LanguageSelectorProps } from '../../types';

const LanguageSelector = ({ className = '' }: LanguageSelectorProps) => {
  const { i18n } = useTranslation();
  

  const changeLanguage = (lng: string) => {
    console.log('Mudando idioma para:', lng);
    
    if (lng === 'pt' || lng === 'pt-BR') {
      localStorage.setItem('i18nextLng', 'pt-BR');
      i18n.changeLanguage('pt-BR');
    } else if (lng === 'en' || lng === 'en-US') {
      localStorage.setItem('i18nextLng', 'en-US');
      i18n.changeLanguage('en-US');
    } else {
      localStorage.setItem('i18nextLng', lng);
      i18n.changeLanguage(lng);
    }
    
    // Forçar uma re-renderização das páginas legais
    setTimeout(() => {
      const currentPath = window.location.pathname;
      if (currentPath === '/privacidade' || currentPath === '/termos') {
        window.dispatchEvent(new Event('languagechange'));
      }
    }, 100);
    
    console.log('localStorage após mudança:', localStorage.getItem('i18nextLng'));
    console.log('i18n.language após mudança:', i18n.language);
  }

  return (
    <div className={`flex space-x-2 ${className}`}>
      <button
        onClick={() => changeLanguage('pt-BR')}
        className={`px-2 py-1 rounded text-sm font-medium transition-colors ${
          i18n.language === 'pt-BR'
            ? 'bg-accent text-white'
            : 'text-dark hover:bg-gray-100'
        }`}
      >
        PT
      </button>
      <button
        onClick={() => changeLanguage('en-US')}
        className={`px-2 py-1 rounded text-sm font-medium transition-colors ${
          i18n.language === 'en-US'
            ? 'bg-accent text-white'
            : 'text-dark hover:bg-gray-100'
        }`}
      >
        EN
      </button>
    </div>
  );
};

export default LanguageSelector;
